/*Pagination*/
.pagination{
    .page-item{
        .page-link{
            border-radius: 50%;
            border: 2px solid #F5F6F7;
            background: #FFF;
            box-shadow: 0px 2px 5px 0px rgba(38, 51, 77, 0.03);
            width: 30px;
            height: 30px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 10px;
            font-weight: 900;
            line-height: 20px;
            color: #6B7A99;
        }
        &:hover,
        &.active{
            .page-link{
                border: 2px solid var(--active);
                background: var(--active);
                color: #FFF;
            }
        }
        &:not(:last-child){
            .page-link{
                margin-right: 8px;
            } 
        }
    }
}

/*Title Components*/
.title-components{
    background-color: rgba(242, 242, 242, 1);
    padding: 8px 12px;
    font-weight: 700;
    font-family: 'Arial Bold', 'Arial', sans-serif;
}

/*Box Button Fixed*/
.box-btn-fixed{
    border-top: 1px solid #ddd;
    bottom: 0;
    left: 0;
    padding: 12px 15px;
    position: fixed;
    z-index: 3;
    width: 100%;
    background: #fff;
}

/*Block Statistical*/
.block-statistical {
    .item{
        @media screen and (max-width: 575px) {
            width: 100%;
        }
    }
    .num{
        display: inline-block;
        color: #5cb85c;
    }
}