/*Button Custom*/

.btn {
    &.btn-themes-action {
        border-radius: 5px;
        color: var(--text-themes-1);
        background: var(--text-white);
        border: 1px solid var(--text-themes-1);
        padding: 2px 10px 2px 5px;
        display: flex;
        align-items: center;
        &.btn-primary {
            color: var(--btn-themes);
            background: rgba(#3361FF, .1);
            border: 1px solid var(--btn-themes);
            &:hover,
            &:active,
            &:focus {
                color: var(--btn-themes);
                background: rgba(#3361FF, .1);
                border: 1px solid var(--btn-themes);
            }
        }
    }
    &.btn-themes {
        border-radius: 20px;
        border: var(--text-themes-1);
        background: var(--text-themes-1);
        color: var(--text-white);
        font-size: 14px;
        font-weight: 900;
        padding: 10px 20px;
        min-width: 120px;
        height: 40px;
        line-height: normal;
        transition: all .2s ease;
        &:hover {
            border: var(--text-themes-hover-1);
            background: var(--text-themes-hover-1);
        }
        &.btn-primary {
            border-color: var(--text-link);
            background: var(--text-link);
            &:hover {
                border-color: var(--text-link-hover);
                background: var(--text-link-hover);
            }
        }
        &.btn-success {
            border-color: var(--text-green);
            background: var(--text-green);
            &:hover {
                border-color: var(--text-green-hover);
                background: var(--text-green-hover);
            }
        }
        &.btn-danger {
            border-color: var(--text-red);
            background: var(--text-red);
            &:hover {
                border-color: var(--text-red-hover);
                background: var(--text-red-hover);
            }
        }
        &.btn-primary-light {
            background: var(--text-white);
            color: var(--text-link);
            border: 2px solid var(--text-link);
            &:hover {
                color: var(--text-link-hover);
                border: 2px solid var(--text-link-hover);
            }
        }
        &.btn-icon {
            width: auto;
            [class*="icon"] {
                color: #fff;
            }
        }

        /*Button Outline Themes*/
        &.btn-outline-secondary{
            background-color: transparent;
            border: 1px solid #6c757d;
            color: #6c757d;
        }
    }
    /* &.btn-themes-light {
        background: var(--btn-themes-light);
        border: 1px solid var(--btn-themes-light);
        color: var(--text-themes-light);
    }
    &.btn-default {
        background: var(--btn-default);
        border: 1px solid var(--btn-default);
        color: var(--text-default);
    }
    &.btn-default-light {
        background: var(--bg-themes);
        border: 1px solid var(--bg-themes);
        color: var(--text-title);
    } */
}


/*Button Themes*/

.btn-primary:hover {
    background-color: var(--btn-themes-hover);
    border-color: var(--btn-themes-hover);
}

.btn-check:active+.btn-primary,
.btn-check:checked+.btn-primary,
.btn-primary.active,
.btn-primary:active,
.show>.btn-primary.dropdown-toggle {
    background-color: var(--btn-themes-active);
    border-color: var(--btn-themes-active);
}


/*Button Themes Light*/

.btn-themes-light:hover {
    color: var( --text-themes-light);
    background-color: var(--btn-themes-light-hover);
    border-color: var(--btn-themes-light-hover);
}

.btn-themes-light:active {
    background-color: var(--btn-themes-light-active);
    border-color: var(--btn-themes-light-active);
}


/*Button Default*/

.btn-default {
    &:hover {
        background-color: var(--btn-default-hover);
        border-color: var(--btn-default-hover);
    }
    &:active {
        background-color: var(--btn-default-active);
        border-color: var(--btn-default-active);
    }
}

.btn-default-light {
    &:hover {
        background-color: var(--btn-default-light-hover);
        border-color: var(--btn-default-light-hover);
    }
    &:active {
        background-color: var(--btn-default-light-active);
        border-color: var(--btn-default-light-active);
    }
    &:focus {
        box-shadow: none;
    }
}