p {
  font-size: 1.1em;
  font-weight: 300;
  line-height: 1.7em; }

a,
a:hover,
a:focus {
  color: inherit;
  text-decoration: none;
  transition: all 0.3s; }

.form-control {
  border: 1px solid #e5e5e5; }

.btn,
.form-control,
.form-select {
  font-size: 0.875rem; }

[class^="icon-"] {
  font-size: 24px;
  color: var(--text-themes-1); }

[class^="fa-"] {
  width: 24px;
  height: 24px;
