#sidebar {
  min-width: 370px;
  max-width: 370px;
  background: var(--bg-themes);
  color: #080C12;
  transition: all .3s ease;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  z-index: 999; }
  @media only screen and (max-width: 600px) {
    #sidebar {
      top: 48px; } }

#sidebar .sidebar-header {
  padding: 8px 10px; }

#sidebar ul.components {
  padding: 20px 24px;
  overflow: auto;
  max-height: calc(100vh - 219px); }
  #sidebar ul.components .list-unstyled li {
    margin-bottom: 0; }
    #sidebar ul.components .list-unstyled li.active a {
      font-weight: 600;
      color: var(--btn-themes); }
      #sidebar ul.components .list-unstyled li.active a:hover {
        color: #fff; }
    #sidebar ul.components .list-unstyled li a {
      border-radius: 0; }
    #sidebar ul.components .list-unstyled li:last-child a {
      border-radius: 0 0 8px 8px; }

#sidebar ul p {
  padding: 10px;
  font-size: 15px;
  display: block; }

#sidebar ul li a {
  padding: 10px 16px;
  font-size: .875rem;
  display: block;
  background-color: rgba(0, 0, 0, 0.05);
  color: var(--text-themes-1);
  border-radius: 8px; }
  #sidebar ul li a [class^="icon-"] {
    font-size: 24px;
    transition: all .3s ease; }
  #sidebar ul li a .icon-arrow-down {
    transform: rotate(0deg);
    transition: all .3s ease; }
  #sidebar ul li a[aria-expanded="true"] {
    border-radius: 8px 8px 0 0; }
  #sidebar ul li a[aria-expanded="false"] .icon-arrow-down {
    transform: rotate(90deg); }
  #sidebar ul li a.collapsed .icon-arrown {
    transform: rotate(90deg); }
  #sidebar ul li a.node-collaspe {
    display: none; }
  #sidebar ul li a[aria-expanded="fasle"] .icon-arrown {
    transform: rotate(90deg); }
  #sidebar ul li a[aria-expanded="true"] .icon-arrown {
    transform: rotate(0deg); }

#sidebar ul li a:hover {
  color: #fff;
  background: var(--active); }
  #sidebar ul li a:hover [class^="icon-"]:before {
    color: #fff; }

#sidebar ul li > a[aria-expanded="true"] {
  /* color: #fff;
    background: #034EA2; 
    [class*="icon"] {
        &:before {
            color: #fff;
        }
    }*/ }

#sidebar ul li > a.active {
  color: #fff;
  background: #034EA2; }
  #sidebar ul li > a.active [class*="icon"]:before {
    color: #fff; }

a[data-toggle="collapse"] {
