.table {
    // --bs-table-hover-bg: #ececec;
    .badge {
        font-size: 1em;
        font-weight: 400;
        &.rounded-circle {
            .badge-themes {
                width: 32px;
                height: 32px;
                display: flex;
                align-items: center;
                justify-content: center;
                font-weight: 500;
            }
        }
        &.badge-avatar {
            font-size: 0.9rem;
        }
        /*Badge Tuyn dng ng vin*/
        &.loc-hs {
            background-color: #1f618d;
        }
        &.thi-tuyen {
            background-color: #117a65;
        }
        &.phong-van {
            background-color: #17a589;
        }
        &.offer {
            background-color: #edbb99;
        }
        &.bi-loai {
            background-color: #eb984e;
        }
        &.tu-choi {
            background-color: #999;
        }
        &.tuyen-dung {
            background-color: #27ae60;
        }
    }
    &.align-top {
        th,
        td {
            vertical-align: top;
        }
    }
    thead {
        tr {
            th {
                &:after {
                    content: none;
                }
            }
        }
    }
    tr {
        .fixed-col {
            &.last {
                /* &:before {
                    content: '';
                    height: 100%;
                    width: 70px;
                    background: linear-gradient(90deg, #FFFFFF 19.97%, rgba(255, 255, 255, 0) 136.03%);
                    position: absolute;
                    right: -70px;
                    top: 0;
                }*/
                &:after {
                    content: '';
                    height: 100%;
                    width: 1px;
                    background-color: #e5e5e5;
                    position: absolute;
                    right: 0;
                    top: 0;
                    z-index: -1;
                    pointer-events: none;
                }
            }
        }
        .fixed-col-row {
            &.last {
                /*&:before {
                    content: '';
                    height: 100%;
                    width: 70px;
                    background: linear-gradient(90deg, #FFFFFF 19.97%, rgba(255, 255, 255, 0) 136.03%);
                    position: absolute;
                    right: -70px;
                    top: 0;
                }*/
                &:after {
                    content: '';
                    height: 100%;
                    width: 1px;
                    background-color: #e5e5e5;
                    position: absolute;
                    right: 0;
                    top: 0;
                    z-index: 1;
                }
            }
        }
        &.bg-white {
            td {
                --bs-table-striped-bg: rgb(255, 255, 255);
            }
        }
        &.bg-gray {
            td {
                --bs-table-bg: #f2f2f2;
            }
        }
        &.sub-white {
            td {
                --bs-table-striped-bg: rgb(255, 255, 255);
                border-top: 1px solid #e5e5e5;
            }
        }
        &.sub-gray {
            td {
                --bs-table-bg: #f2f2f2;
                --bs-table-striped-bg: #f2f2f2;
                border-top: 1px solid #e5e5e5;
            }
        }
        th {
            color: var(--text-themes-1);
            font-weight: 400;
        }
    }
    .dropdown-scrollbar {
        position: absolute;
        z-index: 2;
        .box-search {
            display: none !important;
            background-color: #fff;
            padding: 0.5rem 1rem;
            &.show {
                display: flex !important;
            }
            .box-inner {
                background-color: #f7f7f7;
                border-radius: 4px;
                width: 100%;
            }
            .icon-search {
                position: absolute;
                left: 30px;
            }
            .input-search {
                border: 0;
                background-color: #F7F7FD;
                border-radius: 4px;
                padding-left: 40px;
            }
        }
    }
    .dropdown-menu {
        min-width: 300px;
        max-height: 370px;
        position: relative;
        .dropdown-item {
            padding: 0.5rem 1rem;
            display: flex;
            align-items: center;
            .icon-edit {
                margin-left: auto;
            }
        }
    }
    .dropdown-toggle {
        white-space: normal;
        text-align: left;
    }
}

.table-listing-event{
    max-height: calc(100vh - 248px);
    thead{
        tr{
            th{
                color: var(--text-themes-1);
            }
        }
    }
    tbody{
        tr{
            &:first-child{
                td{
                    color: var(--text-themes-1);
                }
            }
        }
    }
}

.table-create-events{
    tr{
        td{
            &:hover{
                cursor: pointer;
            }
        }
        &.list-events{
            background: green;
            td{
                border-bottom: 1px solid var(--border-color);
            }
            &:nth-last-child(){
                background: red;
                td{
                    border-bottom: 0;
                }
            }
        }
    }
    .dropdown-block-content{
        .icon-arrow-down{
            transition: all .3s ease;
            transform: rotate(-90deg);
        }
        &.collaspe{
            .icon-arrow-down{
                transform: rotate(0);
            }
        }
    }
}
/*Table Fixed Scroll Vertical Horizontal*/

.fixed-vertical-horizontal {
    position: relative;
    /*&:before{
		content: '';
		position: absolute;
		bottom: 0;
		right: -5px;
		z-index: 4;
		background: #ddd;
		height: 100%;
		width: 1px;
	}
	&:after{
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		z-index: 2;
		background: #ddd;
		width: 100%;
		height: 1px;
	}*/
    tr {
        &:nth-child(2) {
            .fixed-row {
                top: 30px;
            }
        }
        &:nth-child(3) {
            .fixed-row {
                top: 60px;
            }
        }
    }
    .fixed-col {
        position: sticky;
        left: 0;
        z-index: 1;
        background: #fff;
        /* &:before {
            content: '';
            position: absolute;
            left: -1px;
            top: -1px;
            width: calc(100% + 2px);
            height: calc(100% + 2px);
            border: 1px solid #ddd;
        } */
    }
    .fixed-row {
        position: sticky;
        top: 0;
        z-index: 2;
        &:after {
            content: '';
            width: 100%;
            height: 1px;
            background: #dee2e6;
            position: absolute;
            left: 0;
            bottom: 0;
        }
    }
    .fixed-col-row {
        position: sticky;
        top: 0;
        left: 0;
        z-index: 3;
    }
    .fixed-row,
    .fixed-col-row {
        /*&:before{
			content: '';
			position: absolute;
			left: -1px;
			top: -1px;
			width: calc(100% + 2px);
			height: calc(100% + 2px);
			border: 1px solid #ddd;
			border-bottom: 2px solid #ddd;
			z-index: 1;
		}*/
    }
    .fixed-footer {
        background: #fff;
        position: sticky;
        bottom: 0;
        z-index: 2;
        border: 1px solid #ddd;
        &:before {
            content: '';
            width: 100%;
            height: 1px;
            display: inline-block;
            position: absolute;
            top: -1px;
            left: 0;
            background: #ddd;
        }
        &:after {
            content: '';
            width: 100%;
            height: 1px;
            display: inline-block;
            position: absolute;
            bottom: 0;
            left: 0;
            background: #ddd;
        }
    }
}


/*End Table Fixed Scroll Vertical Horizontal*/