/*Label Themes*/

.label {
    &.label-themes {
        border-radius: 30px;
        padding: 4px 12px;
        &.label-success {
            background-color: #E6F7E9;
            color: #319E29 !important;
        }
        &.label-danger {
            background-color: rgba(255, 60, 49, 0.1);
            color: #FF3C31 !important;
        }
        &.label-warning {
            background-color: rgba(244, 190, 55, 0.1);
            ;
            color: #FAA71A !important;
        }
        &.label-primary {
            background-color: #D6E9FF;
            color: #1983FC !important;
        }
    }
}

/*Label Color Custom*/
.label-theme{
    border-radius: 30px;
    display: inline-flex;
    padding: 4px 8px 4px 15px;
    border: 2px solid var(--text-themes-1);
    line-height: 30px;
    [class^=icon-]{
        line-height: 30px;
    }
}

.bg-organe-light{
    border: 2px solid #F63;
    background: rgba(255, 102, 51, 0.05);
}

.bg-purple-light{  
    border: 2px solid #83F;
    background: rgba(136, 51, 255, 0.05);
}

.bg-warning-light{
    border: 2px solid #FFCB33;
    background: rgba(255, 203, 51, 0.05);
}

.bg-success-light{
    border: 2px solid #29CC39;
    background: rgba(41, 204, 57, 0.05);
}

.bg-info-light{
    border-radius: 30px;
    border: 2px solid #33BFFF;
    background: rgba(51, 191, 255, 0.05);
}

.bg-danger-light{
    border: 2px solid #E62E7B;
    background: rgba(230, 46, 123, 0.05);
}

.bg-brown-light{
    border: 2px solid #CC7429;
    background: rgba(204, 116, 41, 0.05);
}
/*Input themes*/

.input-primary {
    color: #034EA2;
    border: 1px solid #e5e5e5;
    border-radius: 2px;
    &:hover,
    &:focus {
        color: #034EA2;
        border: 1px solid #1983FC;
        outline: none;
        box-shadow: none;
    }
}

.input-success {
    color: #009A36;
    border: 1px solid #e5e5e5;
    border-radius: 4px;
    &:hover,
    &:focus {
        border: 1px solid #50B848;
        outline: none;
        box-shadow: none;
        color: #009A36;
    }
}

