/*Line Height*/

.lh-35 {
    line-height: 2.188rem;
}

$index: 50;
@while $index <=400 {
    .w#{$index} {
        width: #{$index}px !important;
    }
    .min-w#{$index} {
        min-width: #{$index}px !important;
    }
    .max-w#{$index} {
        max-width: #{$index}px !important;
    }
    $index: ($index + 10);
}

