/*Button Create*/
.btn-create{
    .btn{
        padding: 2px 12px 2px 6px;
        border-color: var(--active);
        color: var(--active);
        [class*="icon-"]{
            font-size: 24px;
        }
        &:hover,
        &:focus,
        &:active{
            border-color: var(--active);
            color: var(--active);
        }
        &:not(:last-child){
            display: flex;
            align-items: center;
            background: rgba(#3361FF, .1);
        }
        &.dropdown-toggle{
            background: #fff;
            display: flex;
            align-items: center;
            padding: 2px;
            &:after{
                content: none;
            }
        }
    }
}

/*textarea Form Input*/
.input-textarea{
    position: relative;
    .icon-attach{
        position: absolute;
        right: 10px;
        bottom: 10px;
        font-size: 14px;
    }
}
textarea{
    &.form-input{
        border: 1px solid #dadee6;
        border-radius: 0.3rem;
        padding: 0.375rem 0.75rem;
        outline: none;
        resize: none;
    }
}