/*Input Text*/

.input-text,
input.input-text,
select.input-text,
textarea.input-text {
    border: 0 !important;
    border-bottom: 1px dashed #ccc !important;
    box-shadow: none;
    background: transparent;
}


/*Custom Form Bootstrap*/

.form-control,
.form-select {
    border-radius: .3rem;
    border: 1px solid #DADEE6;
    height: 40px;
    &:focus {
        border: 1px solid #86b7fe;
        box-shadow: none;
    }
}

.form-select {
    background-size: 7px;
}

.form-check {
    .form-check-input {
        margin-top: 0.18em;
    }
}

textarea.form-control {
    height: auto;
    resize: none;
    &.form-input {
        padding: 8.5px 12px;
        min-height: 40px;
        max-height: 250px;
    }
}


/*End Custom Form Bootstrap*/

.box-filter {
    /* .form-control,
    .form-select {
        border-radius: 0;
    } */
    .fixed-height {
        height: 35px !important;
    }
    .btn {
        color: var(--text-themes-1);
        &:focus {
            box-shadow: none;
        }
        [class*="icon"] {
            font-size: 24px;
        }
        span {
            margin-left: 4px;
            margin-right: 4px;
        }
        .icon-arrow-down {
            width: 24px;
            height: 24px;
            font-size: 30px;
            display: flex;
            align-items: center;
        }
    }
    .dropdown-scrollbar {
        position: absolute;
        z-index: 4;
        .box-search {
            display: none !important;
            background-color: #fff;
            padding: 0.5rem 1rem;
            &.show {
                display: flex !important;
            }
            .box-inner {
                background-color: #f7f7f7;
                border-radius: 4px;
                width: 100%;
            }
            .icon-search {
                position: absolute;
                left: 30px;
            }
            .input-search {
                border: 0;
                background-color: #F7F7FD;
                border-radius: 4px;
                padding-left: 40px;
            }
        }
    }
}

.input-group-addon {
    position: absolute;
    right: 1px;
    top: 1px;
    z-index: 3;
    .input-group-text {
        border-radius: 0;
        background-color: #fff;
        border: 1px solid transparent;
        padding: 0.47rem 0.75rem;
    }
}

.btn-themes,
.btn-themes-light {
    border-radius: .125rem;
}

.form-grid {
    grid-template-columns: repeat(5, 1fr);
    grid-gap: 0 16px;
    @media screen and (max-width: 1399px) {
        grid-template-columns: repeat(3, 1fr);
    }
    @media screen and (max-width: 1023px) {
        grid-template-columns: repeat(2, 1fr);
    }
    @media screen and (max-width: 600px) {
        grid-template-columns: repeat(1, 1fr);
    }
    &.grid-4 {
        grid-template-columns: repeat(4, 1fr);
        @media screen and (max-width: 1399px) {
            grid-template-columns: repeat(3, 1fr);
        }
        @media screen and (max-width: 1023px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media screen and (max-width: 600px) {
            grid-template-columns: repeat(1, 1fr);
        }
        .time {
            grid-column: 4/6;
            @media screen and (max-width: 1399px) {
                grid-column: unset;
            }
        }
        .dept {
            grid-column: 3/6;
            @media screen and (max-width: 1399px) {
                grid-column: unset;
            }
        }
        .link {
            grid-column: 1/3;
        }
        .council {
            grid-column: 3/6;
            @media screen and (max-width: 1399px) {
                grid-column: unset;
            }
        }
    }
    .address {
        grid-column: 1/3;
        @media screen and (max-width: 1399px) {
            grid-column: unset;
        }
    }
    .council {
        grid-column: 3/6;
        @media screen and (max-width: 1399px) {
            grid-column: unset;
        }
    }
    .note {
        grid-column: 1/6;
        @media screen and (max-width: 1399px) {
            grid-column: unset;
        }
    }
    .short-note {
        grid-column: 4/6;
        @media screen and (max-width: 1399px) {
            grid-column: unset;
        }
    }
}

.box-field {
    &__inner {
        padding: 4px 16px;
        background-color: #EBF4FF;
        border-radius: 2px;
        margin-right: 8px;
        margin-bottom: 8px;
        float: left;
    }
}

