:root {
    /*Color Themes*/
    --bg-themes: #F2F3F5;
    --text-themes: #0D111A;
    --text-themes-1: #6B7A99;
    --text-themes-hover-1: #606e8b;
    --text-link: #3361FF;
    --text-link-hover: #1a4dff;
    --btn-themes: #034EA2;
    --border-color: #DADEE6;
    --active: #3361ff;
    --text-white: #fff;
    --text-red: #E62E2E;
    --text-red-hover: #E62E2E
    --text-green: #29CC39;
    --text-green-hover: #29CC39
    --border-color: #EDEFF2;
    
    /*Button Themes*/
    --btn-themes: #3361FF;
}


/*Min Width && Max Width*/

$index: 50;
@while $index <=500 {
    .min-w#{$index} {
        min-width: #{$index}px !important;
    }
    .max-w#{$index} {
        max-width: #{$index}px !important;
    }
    .w#{$index} {
        width: #{$index}px !important;
    }
    $index: ($index + 10);
}


/*Spacing Margin && Padding*/

$index: 10;
@while $index <=50 {
    .ps-#{$index} {
        padding-left: #{$index}px !important;
    }
    .pe-#{$index} {
        padding-right: #{$index}px !important;
    }
    $index: ($index + 5);
}
