/* Scrollbar custom Works on Firefox */

* {
    scrollbar-width: thin;
    scrollbar-color: rgb(192, 192, 192) white;
}


/* Works on Chrome, Edge, and Safari */

*::-webkit-scrollbar {
    width: 12px;
}

*::-webkit-scrollbar-track {
    background: white;
}

*::-webkit-scrollbar-thumb {
    background-color: #E5E5E5;
    border-radius: 20px;
    border: 3px solid white;
}

.badge-notification {
    position: absolute;
    font-size: .6rem;
    margin-left: -0.85rem;
    margin-top: 0;
    padding: 0.2em 0.45em;
    background-color: var(--active);
}

.badge-user {
    background: #F2F3F5;
    color: #0D111A;
    border-radius: 12px;
    align-items: center;
    .badge {
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
    }
}

#content {
    width: 100%;
    padding: 20px;
    min-height: 100vh;
    transition: all 0.3s
}

.content-wrapper {
    position: relative;
    padding: 1.5rem;
    color: var(--text-themes);
    &.show-list-edit {
        .column-left-view {
            transform: translateX(calc(-100% - 20px));
        }
        .column-center-view {
            max-width: 100% !important;
            margin-left: -310px;
        }
        .view-candidate {
            >.ic-close {
                top: 22px;
                left: 30px;
            }
            .box-avatar {
                padding-left: 45px;
            }
        }
    }
    /*Dropzone*/
    .dropzone {
        min-height: auto;
        border: 0;
        background: transparent;
        padding: 0;
        .dz-message {
            margin: 0;
        }
        .dz-preview {
            margin: 0;
            min-height: auto;
            .dz-details {
                padding: 0.5em;
                .dz-size {
                    margin-bottom: 2px;
                }
            }
            &.dz-file-preview {
                .dz-image {
                    border-radius: 8px;
                }
            }
            .dz-image {
                height: 50px;
            }
            .dz-success-mark,
            .dz-error-mark {
                margin-top: -14px;
                margin-left: -14px;
                svg {
                    width: 28px;
                    height: 28px;
                }
            }
        }
    }
}

.content {
    width: 100%;
    &.bottom-bar{
        padding-bottom: 65px;
    }
    .navbar {
        padding: 5px 10px;
        position: sticky;
        top: 0;
        z-index: 999;
        .btn-collaspe {
            display: flex;
            align-items: center;
            padding: 3px 12px;
        }
        .icon-arrown,
        .icon-bell {
            &:before {
                color: #fff;
            }
        }
    }
    .logo {
        width: 0;
        margin: 0;
        opacity: 0;
        visibility: hidden;
        transition: all .2s ease;
        img {
            height: 38px;
        }
    }
}

.bg-themes {
    background-color: var(--bg-themes);
}

.bg-popup {
    background-color: var(--bg-popup);
}


/*Text Color*/

.text-orange-themes {
    color: var(--text-orange-themes);
}

.text-primary-themes {
    color: var(--text-primary-themes);
}

.text-red-themes {
    color: var(--text-red-themes);
}

.btn-collaspe {
    .icon-menu {
        font-size: 24px;
        &:before {
            color: #fff;
        }
    }
}

.circle-down {
    border: 1.5px solid #515A6E;
    border-radius: 50%;
    width: 16px;
    height: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
}

.hidden-arrow {
    &.dropdown-toggle {
        &:after {
            display: none;
        }
    }
}

.dropdown-filter {
    border: 0;
    box-shadow: 0px 4px 25px rgba(81, 90, 110, 0.15);
    border-radius: 2px;
    padding: 0;
    overflow: auto;
}


/*Box filter*/

.box-filter {
    .dropdown-menu {
        .dropdown-item {
            padding: 0.4375rem;
            font-size: 0.9375rem;
            line-height: 1.5rem;
            &:hover {
                background-color: #5897fb;
                color: #fff;
                font-weight: 500;
            }
        }
    }
    .dropdown-scrollbar {
        .dropdown-menu {
            max-height: 370px;
            position: relative;
        }
    }
    .icon-filter {
        font-size: 18px;
    }
    .icon-add {
        &:before {
            color: #064996;
        }
    }
    form {
        >div {
            @media screen and (max-width: 480px) {
                width: 100%;
            }
            .dropdown-toggle,
            .dropdown-link {
                @media screen and (max-width: 480px) {
                    width: 100%;
                    text-align: left;
                }
            }
        }
    }
}

.dropdown-filter {
    .search {
        padding: 0.4375rem;
        .box-search {
            background-color: #F7F7FD;
            border-radius: 4px;
            position: relative;
            .icon-search {
                position: absolute;
                left: 10px;
                top: 6px;
                font-size: 24px;
            }
            input {
                background-color: #F7F7FD;
                padding-left: 40px;
                border: 0;
            }
        }
    }
    .group {
        background-color: var(--bg-popup);
        a {
            color: var(--text-themes-light);
            font-weight: 500;
        }
    }
}


/*Box Datetime*/

.box-datetime {
    display: flex;
    align-items: center;
    border-radius: .3rem;
    position: relative;
    [class^="icon"] {
        position: absolute;
        left: 8px;
        font-size: 18px;
    }
    .form-control {
        padding-left: 32px !important;
    }
}


/*Box User*/

.box-user {
    @extend .box-datetime;
}

.title {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    overflow: hidden;
    &.line-1 {
        -webkit-line-clamp: 1;
        min-height: 18px;
    }
    &.line-2 {
        -webkit-line-clamp: 2;
        min-height: 36px;
    }
}

.icon-group {
    color: var(--text-themes-1);
    [class^="icon"] {
        font-size: 16px;
    }
    span {
        font-size: 14px;
        font-family: Roboto, sans-serif;
    }
}


/*Update Event*/

.wrap-update-event {
    width: calc(100% - 370px);
    height: calc(100vh - 49px);
    position: fixed;
    background: #fff;
    top: 49px;
    right: 0;
    z-index: 9;
    padding: 1.5rem;
    transition: all .3s ease;
    transform: translateX(100%);
    @media screen and (max-width: 767px) {
        left: 84px;
        width: calc(100% - 84px);
    }
    &.active {
        transform: translate(0);
    }
    .close-update {
        position: absolute;
        right: 8px;
        top: 8px;
        @media screen and (max-width: 767px) {
            right: 4px;
            top: 4px;
        }
        &:hover {
            cursor: pointer;
        }
        .icon-close {
            font-size: 32px;
        }
    }
    .col-right {
        border-left: 1px solid #dee2e6;
        @media screen and (max-width: 1199px) {
            border-left: 0;
        }
    }
    .select2-container {
        width: 100% !important;
    }
}

.update-events {
    &:hover {
        cursor: pointer;
    }
}


/*Download file PDF*/

.download-pdf {
    border-radius: 5px;
    border: 1px solid #6B7A99;
    color: #6B7A99;
    [class^="icon-"] {
        color: #6B7A99;
    }
    .icon-pdf {
        color: #E64B17;
        margin-right: 5px;
    }
    .icon-view {
        width: 44px;
        height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .icon-download {
        width: 44px;
        height: 44px;
        padding: 5px;
        border-left: 1px solid #6B7A99;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .file-info {
        line-height: normal;
    }
    .file-name {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 1;
        overflow: hidden;
        width: 112px;
        min-height: 18px;
    }
}


/*Box Comment*/

.box-comment {
    .block-title {
        font-size: 16px;
        line-height: 30px;
        margin-bottom: 16px;
    }
    .block-comment {
        overflow-y: auto;
        max-height: calc(100vh - 410px);
        padding-top: 20px;
    }
    .user-create-checklist {
        position: relative;
        padding-left: 22px;
        &:before {
            content: '';
            width: 3px;
            height: 3px;
            border-radius: 50%;
            position: absolute;
            top: 9px;
            left: 9px;
            background: var(--text-themes-1);
        }
    }
    .box-user-comment {
        position: relative;
        font-size: 13px;
        .time {
            position: absolute;
            bottom: calc(100% - 3px);
            left: 35px;
            font-size: 12px;
        }
        &:hover {
            .btn-group-icons {
                display: flex;
            }
        }
        &.admin {
            .comment {
                background: #D1E3FF;
            }
        }
        &.guest {
            padding-left: 45px;
            .time {
                left: 80px;
            }
            .comment {
                background: #EBEDEE;
            }
        }
    }
    .user-comment {
        position: relative;
        .avatar {
            width: 40px;
            height: 40px;
            margin-right: 8px;
            display: flex;
            align-items: center;
            justify-content: center;
            flex-shrink: 0;
            font-size: 16px;
            font-weight: 600;
            line-height: 1;
            color: #fff;
            border-radius: 50%;
        }
        .comment {
            border-radius: 10px;
            padding: 8px 12px;
            .txt-name {
                font-size: 14px;
            }
        }
        .content-comment {
            color: var(--text-themes);
            .nickname {
                color: var(--text-link);
            }
        }
        .txt-name {
            &.reply {
                font-size: 12px;
                padding-left: 8px;
                position: relative;
                &:before {
                    content: '';
                    background: #a3a4a6;
                    position: absolute;
                    height: 15px;
                    width: 1px;
                    left: 0;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }
        }
    }
    .btn-group-icons {
        border-radius: 3px;
        position: absolute;
        top: 0;
        left: calc(100% + 8px);
        display: none;
        .icons {
            padding: 0 5px;
            color: #A3A4A6;
            &:hover {
                color: var(--text-link);
            }
        }
    }
    /*Box Area Comment*/
    .box-area-comment {
        margin-top: auto;
        .box-input-text {
            position: relative;
        }
        .form-input {
            padding-right: 35px;
        }
        .icon-send {
            position: absolute;
            right: 10px;
            bottom: 8px;
        }
    }
}


/*Crrate Events*/

.create-events {
    .group-icon-check {
        .items {
            width: 10px;
            height: 10px;
            border-radius: 50%;
            &.before,
            &.finish {
                border: 2px solid var(--text-themes-1);
            }
            &.in-progress {
                border: 1px solid var(--text-themes-1);
                background: var(--text-white);
                position: relative;
                &:after {
                    content: '';
                    width: 4px;
                    height: 4px;
                    border-radius: 50%;
                    background: var(--text-themes-1);
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                }
            }
            &.active {
                &.before {
                    border-color: var(--text-red);
                }
                &.in-progress {
                    border-color: var(--text-red);
                    &:after {
                        content: '';
                        background: var(--text-red);
                    }
                }
                &.finish {
                    border: 0;
                    background: var(--text-red);
                }
            }
        }
    }
}


/*Icon Action*/

.icon-check {
    &:hover,
    &.active {
        color: var(--text-green);
    }
}

.icon-reply {
    &:hover,
    &.active {
        color: var(--text-red);
    }
}

.icon-trash {
    &:hover {
        color: var(--text-link);
    }
}

