* {
    margin: 0;
    padding: 0;
    border: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    color: var(--text-themes-1);
    font-size: 0.875rem;
    line-height: 160%;
    overflow-x: hidden;
}

p {
    font-size: 1.1em;
    font-weight: 300;
    line-height: 1.7em;
}

a,
a:hover,
a:focus {
    color: inherit;
    text-decoration: none;
    transition: all 0.3s
}

.form-control {
    border: 1px solid #e5e5e5;
}

.btn,
.form-control,
.form-select {
    font-size: 0.875rem;
}

[class^="icon-"]{
    font-size: 24px;
    color: var(--text-themes-1);
}

[class^="fa-"]{
    width: 24px;
    height: 24px;
    line-height: 24px !important;
    color: var(--text-themes-1);
}

[class*="title"]{
    color: var(--text-themes);
}